<template>
  <div>
    <!-- Модальное окно -->
    <div class="modal-window" v-if="show === true" @click="hideModalWindow">
      <div @click.stop class="modal-window-content">
        <slot></slot>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "modal-window",
  props: {
    show: {
      type: Boolean,
      default: false,
    },
  },
  methods: {
    hideModalWindow() {
      this.$emit("hide", false);
    },
  },
};
</script>

<style scoped lang="scss">
@import "../../main.scss";

.modal-window {
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background: rgb(0, 0, 0, 0.6);
  z-index: 10;
}
.modal-window-content {
  width: 50%;
  margin: auto;
  background: $teal-800;
  border-radius: 1rem;
  padding: 2rem;
}
</style>
