<template>
  <!-- v-bind:value="value" -->
  <!-- @select="$emit('chooseOption', event.target.value)" -->
  <!-- @change="value = $event.target.value" -->
  <select
    class="form-select-sm border border-primary"
    v-bind="value"
    @change="changeOption"
  >
    <option disabled selected value="">Choose option</option>
    <option v-for="option in options" :key="option.value" :value="option.value">
      {{ option.name }}
    </option>
  </select>
</template>

<script>
export default {
  name: "select-task",
  props: {
    value: {
      type: String,
    },
    options: {
      type: Array,
      default: () => [],
    },
  },
  methods: {
    changeOption(event) {
      this.$emit("update:value", event.target.value);
    },
  },
};
</script>

