<template>
  <div class="d-flex flex-column align-items-center">
    <h3 class="heading mb-5">My Tasks:</h3>
    <div v-if="tasks.length > 0">
      <ul class="list-group">
        <TransitionGroup name="list" tag="ul">
          <li
            class="task d-flex align-items-center gap-3 list-group-item mb-2"
            v-for="task of tasks"
            :key="task.id"
            :class="{ 'finished-task': task.isCompleted }"
          >
            <span>{{ task.id }}</span>
            <div>
              <input
                v-if="task.isEditing"
                @keyup.enter="$emit('changeText', task.text)"
                v-bind:value="task.text"
                @input="task.text = $event.target.value"
                type="text"
              />
              <span
                v-else
                @click="task.isCompleted = !task.isCompleted"
                class="title fw-bold"
                >{{ task.text }}</span
              >
            </div>

            <div>
              <input
                v-if="task.isEditing"
                @keyup.enter="$emit('changeDescription', task.description)"
                v-bind:value="task.description"
                @input="task.description = $event.target.value"
                type="text"
              />
              <span v-else @click="task.isCompleted = !task.isCompleted">{{
                task.description
              }}</span>
            </div>
            <button
              @click="$emit('edit', task.text)"
              class="edit btn"
              title="Edit"
            >
              <svg
                xmlns="http://www.w3.org/2000/svg"
                width="16"
                height="16"
                fill="currentColor"
                class="bi bi-pencil"
                viewBox="0 0 16 16"
              >
                <path
                  d="M12.146.146a.5.5 0 0 1 .708 0l3 3a.5.5 0 0 1 0 .708l-10 10a.5.5 0 0 1-.168.11l-5 2a.5.5 0 0 1-.65-.65l2-5a.5.5 0 0 1 .11-.168l10-10zM11.207 2.5 13.5 4.793 14.793 3.5 12.5 1.207 11.207 2.5zm1.586 3L10.5 3.207 4 9.707V10h.5a.5.5 0 0 1 .5.5v.5h.5a.5.5 0 0 1 .5.5v.5h.293l6.5-6.5zm-9.761 5.175-.106.106-1.528 3.821 3.821-1.528.106-.106A.5.5 0 0 1 5 12.5V12h-.5a.5.5 0 0 1-.5-.5V11h-.5a.5.5 0 0 1-.468-.325z"
                />
              </svg>
            </button>
            <button
              @click="$emit('delete', task.text)"
              class="delete btn"
              title="Delete"
            >
              <svg
                xmlns="http://www.w3.org/2000/svg"
                width="16"
                height="16"
                fill="currentColor"
                class="bi bi-trash3"
                viewBox="0 0 16 16"
              >
                <path
                  d="M6.5 1h3a.5.5 0 0 1 .5.5v1H6v-1a.5.5 0 0 1 .5-.5ZM11 2.5v-1A1.5 1.5 0 0 0 9.5 0h-3A1.5 1.5 0 0 0 5 1.5v1H2.506a.58.58 0 0 0-.01 0H1.5a.5.5 0 0 0 0 1h.538l.853 10.66A2 2 0 0 0 4.885 16h6.23a2 2 0 0 0 1.994-1.84l.853-10.66h.538a.5.5 0 0 0 0-1h-.995a.59.59 0 0 0-.01 0H11Zm1.958 1-.846 10.58a1 1 0 0 1-.997.92h-6.23a1 1 0 0 1-.997-.92L3.042 3.5h9.916Zm-7.487 1a.5.5 0 0 1 .528.47l.5 8.5a.5.5 0 0 1-.998.06L5 5.03a.5.5 0 0 1 .47-.53Zm5.058 0a.5.5 0 0 1 .47.53l-.5 8.5a.5.5 0 1 1-.998-.06l.5-8.5a.5.5 0 0 1 .528-.47ZM8 4.5a.5.5 0 0 1 .5.5v8.5a.5.5 0 0 1-1 0V5a.5.5 0 0 1 .5-.5Z"
                />
              </svg>
            </button>
          </li>
        </TransitionGroup>
      </ul>
    </div>
    <div v-else class="bg-white px-4 py-3">
      <p class="mb-0">No tasks for today 💃🏻</p>
    </div>
  </div>
</template>

<script>
export default {
  name: "TasksList",
  props: { tasks: { type: Array, required: true } },
};
</script>

<style scoped lang="scss">
@import "../main.scss";

.heading {
  color: $teal;
}
.task {
  cursor: pointer;
  color: $gray-600;
  background: $teal-900;
  border: none;
  border-bottom: 1px solid $teal-800;
}
.finished-task {
  text-decoration: line-through;
  color: $gray-500;
}
.title {
  color: $teal;
}
.edit {
  background: $teal-600;
  color: $white;
}
.edit:hover {
  background: $teal-700;
  color: $white;
}
.delete {
  background: $teal-700;
  color: $white;
}
.delete:hover {
  background: $teal-800;
  color: $white;
}
.list-enter-active,
.list-leave-active {
  transition: all 0.5s ease;
}
.list-enter-from,
.list-leave-to {
  opacity: 0;
  transform: translateX(130px);
}

.list-move {
  transition: transform 0.5s ease;
}
</style>
