<template>
  <div>
    <div>
      <h3 class="heading mb-3">Create new Task</h3>
      <!-- Создание новой задачи  -->
      <form class="card-body d-flex flex-column" @submit.prevent>
        <input
          v-model="task.text"
          type="text"
          class="form-control mb-3"
          placeholder="Task title"
        />
        <input
          v-model="task.description"
          type="text"
          class="form-control mb-3"
          placeholder="Task description..."
        />
        <button @click="addTask" class="save btn">Save</button>
      </form>
    </div>
  </div>
</template>

<script>
export default {
  name: "CreateTask",
  props: { tasks: { type: Array, required: true } },
  data() {
    return {
      task: {
        id: "",
        text: "",
        description: "",
        isCompleted: false,
        isEditing: false,
      },
    };
  },
  methods: {
    addTask() {
      if (this.tasks.length > 0) {
        this.task.id = this.tasks[this.tasks.length - 1].id + 1;
      } else {
        this.task.id = 1;
      }

      this.$emit("create", this.task);
      this.task = {
        id: "",
        text: "",
        description: "",
        isCompleted: false,
        isEditing: false,
      };
    },
  },
};
</script>

