<template>
  <div>
    <div>
      <h2 class="heading text-center my-5">Tasks Page</h2>
      <div class="d-flex justify-content-between">
        <!-- Сортировка по Id, Наименованию, Содержанию -->
        <!-- <select-task v-model="selectedSort" :options="sortOptions" /> -->
        <!-- Кнопка для открытия формы создания задачи -->
        <button class="add btn" @click="openModalWindow">Add Task</button>
      </div>

      <ModalWindow
        :show="modalWindowVisible"
        @hide="modalWindowVisible = false"
      >
        <CreateTask :tasks="tasks" @create="addTask" />
      </ModalWindow>

      <TasksList
        :tasks="sortedTasks"
        @delete="deleteTask"
        @edit="changeEditing"
        @changeText="editText"
        @changeDescription="editDescription"
      />
    </div>
  </div>
</template>
<script>
import CreateTask from "@/components/CreateTask.vue";
import TasksList from "@/components/TasksList.vue";
import ModalWindow from "@/components/UI/ModalWindow.vue";
import SelectTask from "@/components/UI/SelectTask.vue";

export default {
  name: "TasksPage",

  components: {
    SelectTask,
    ModalWindow,
    CreateTask,
    TasksList,
  },
  // beforeCreate() {
  //   this.$store.commit("initialiseStore");
  // },

  data() {
    return {
      tasks: [
        {
          id: 1,
          text: "New technology",
          description: "Try to start learning new technology every Monday",
          isCompleted: false,
          isEditing: false,
        },
        {
          id: 2,
          text: "English",
          description: "Master English skills with native speaker",
          isCompleted: false,
          isEditing: false,
        },
        {
          id: 3,
          text: "Vue.js",
          description: "Learn Vue.js 2 and Vue.js 3",
          isCompleted: false,
          isEditing: false,
        },
      ],
      editValue: "",
      modalWindowVisible: false,
      selectedSort: "",
      sortOptions: [
        { value: "id", name: "by id" },
        { value: "text", name: "by title" },
        { value: "description", name: "by description" },
      ],
    };
  },
  // computed: {
  //   tasks() {
  //     return this.$store.state.tasks;
  //   },
  // },

  computed: {
    sortedTasks() {
      console.log(this.selectedSort);
      return [...this.tasks].sort((task1, task2) =>
        task1[this.selectedSort]?.localeCompare(task2[this.selectedSort])
      );
    },
  },

  methods: {
    openModalWindow() {
      this.modalWindowVisible = true;
    },
    addTask: function (task) {
      this.tasks.push(task);
      this.modalWindowVisible = false;
    },
    deleteTask(taskText) {
      this.tasks = this.tasks.filter((task) => {
        return task.text !== taskText;
      });
    },
    changeEditing(taskText) {
      this.editValue = taskText;
      this.tasks = this.tasks.map((task) => {
        if (task.text === taskText) {
          task.isEditing = !task.isEditing;
        }
        return task;
      });
    },
    editText(taskText) {
      this.tasks = this.tasks.map((task) => {
        if (task.text === taskText) {
          task.isEditing = !task.isEditing;
          // this.editValue = task.text;
          taskText = this.editValue;
        }
        return task;
      });
    },
    editDescription(taskText) {
      this.tasks = this.tasks.map((task) => {
        if (task.description === taskText) {
          task.isEditing = !task.isEditing;
          taskText = this.editValue;
        }
        return task;
      });
    },
  },
  mounted() {
    if (localStorage.tasks) {
      this.tasks = JSON.parse(localStorage.tasks);
    }
  },
  watch: {
    tasks(newTasks) {
      localStorage.tasks = JSON.stringify(newTasks);
    },
  },
};
</script>

